import { Fragment } from 'react';
import { Divider, Grid, Typography } from '@mui/material';

import useAuth from 'hooks/useAuth';
import SubCard from 'ui-component/cards/SubCard';
import LogoReport from 'assets/img/LogoReport.png';
import { gridSpacing } from 'store/constant';
import { ColorDrummondltd } from 'themes/colors';
import { FormatDate } from 'components/helpers/Format';

const QuestionnaireTos = ({ title = '', text = '' }) => {
    return (
        <Fragment>
            <Grid item xs={9}>
                <Typography align="justify" fontSize={10}>{title}</Typography>
            </Grid>
            <Grid item xs={3}>
                <Typography align="right" variant="h6">{text}</Typography>
            </Grid>
        </Fragment>
    )
}


const ReportPage3 = ({ datos = [], lsDataUser = [] }) => {
    const { user } = useAuth();

    return (
        <SubCard>
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                    <Grid container spacing={1}>
                        <Grid item xs={3}>
                            <img src={LogoReport} height={50} />
                        </Grid>

                        <Grid item xs={6}>
                            <Typography variant="h5" align="center"><b>DIVISIN MDICA</b></Typography>
                            <Typography variant="h5" align="center"><b>CUESTIONARIO DE SNTOMAS RESPIRATORIOS</b></Typography>
                            <Typography variant="h5" align="center"><b>{datos.datos}</b></Typography>
                        </Grid>

                        <Grid item xs={3}>
                            <Typography variant="h5" align="center"><b>SIG-0409</b></Typography>
                            <Typography variant="h6" align="center"><b>Versin 06</b></Typography>
                        </Grid>
                    </Grid>
                </Grid>

                <Grid item xs={12}>
                    <Divider sx={{ border: 2, borderRadius: 1, background: ColorDrummondltd.RedDrummond, color: ColorDrummondltd.RedDrummond }} />
                </Grid>

                <Grid item xs={12}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <Grid container spacing={1}>
                                <Grid item xs={12}>
                                    <Divider />
                                </Grid>
                                <Grid item xs={12}>
                                    <Grid container spacing={0.2}>

                                        <QuestionnaireTos key={1} title="9. PROBLEMAS DEL CORAZN?" text={datos.problemCoraASintR} />

                                        <Grid item xs={2}>
                                            <Typography fontSize={10}><b>- ESPECIFIQUE</b></Typography>
                                        </Grid>
                                        <Grid item xs={10}>
                                            <Typography align='right' variant="h6">{datos.problemCoraBSintR}</Typography>
                                        </Grid>

                                        <QuestionnaireTos key={3} title="- HA RECIBIDO TRATAMIENTO POR ESTA CAUSA EN LOS LTIMOS DIEZ AOS?" text={datos.problemaCoraCSintR} />
                                        <QuestionnaireTos key={4} title="10. PRESIN ALTA (RECUERDE QUE DEBE SER CONFIRMADA POR EL MDICO)" text={datos.presionAltaASintR} />
                                        <QuestionnaireTos key={5} title="- HA RECIBIDO TRATAMIENTO POR ESTA CAUSA EN LOS LTIMOS DIEZ AOS?" text={datos.presionAltaBSintR} />
                                    </Grid>
                                </Grid>

                                <Grid item xs={12}>
                                    <Divider />
                                </Grid>

                                <Grid item xs={12}>
                                    <Typography variant='h6'><b>10. HISTORIA OCUPACIONAL</b></Typography>
                                </Grid>

                                <Grid item xs={12}>
                                    <Divider />
                                </Grid>

                                <Grid item xs={12}>
                                    <Grid container spacing={0.2}>
                                        <QuestionnaireTos key={1} title="A. HA TRABAJADO TIEMPO COMPLETO (8 HORAS A LA SEMANA O MS) POR 6 MESES O MS?" text={datos.historiaOcupASintR} />
                                        <QuestionnaireTos key={2} title="B. HA TRABAJADO AL MENOS DURANTE 6 MESES EN UN EMPLEO DONDE TUVO EXPOSICIN A POLVOS?" text={datos.historiaOcupBSintR} />
                                        <Grid item xs={3}>
                                            <Typography fontSize={10}><b>- ESPECIFIQUE EMPLEO O INDUSTRIA</b></Typography>
                                        </Grid>
                                        <Grid item xs={9}>
                                            <Typography align='right' variant="h6">{datos.historiaOcupB1SintR}</Typography>
                                        </Grid>

                                        <QuestionnaireTos key={4} title="- TOTAL AOS TRABAJADOS:" text={datos.historiaOcupB2SintR} />
                                        <QuestionnaireTos key={5} title="- LA EXPOSICIN FUE:" text={datos.nameHistoriaOcupB3SintR} />

                                        <QuestionnaireTos key={6} title="C. CUL HA SIDO SU OCUPACIN O TRABAJO USUAL EN EL QUE HA LABORADO POR MS TIEMPO?" text={datos.historiaOcupD2SintR} />
                                        <Grid item xs={4}>
                                            <Typography fontSize={10}>- EMPLEO Y OCUPACIN</Typography>
                                        </Grid>
                                        <Grid item xs={8}>
                                            <Typography align='right' variant='h6'>{datos.historiaOcupD1SintR}</Typography>
                                        </Grid>
                                        <Grid item xs={4}>
                                            <Typography fontSize={10}>- NEGOCIO, CAMPO O INDUSTRIA</Typography>
                                        </Grid>
                                        <Grid item xs={8}>
                                            <Typography align='right' variant='h6'>{datos.historiaOcupD3}</Typography>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>

                        <Grid item xs={12}>
                            <Grid container spacing={1}>
                                <Grid item xs={12}>
                                    <Divider />
                                </Grid>

                                <Grid item xs={12}>
                                    <Typography variant='h6'><b>11. TABAQUISMO</b></Typography>
                                </Grid>

                                <Grid item xs={12}>
                                    <Divider />
                                </Grid>

                                <Grid item xs={12}>
                                    <Grid container spacing={0.2}>
                                        <QuestionnaireTos key={1} title="A. HA FUMADO CIGARRILLOS, PIPA O TABACO (AL MENOS UNO(A) AL DA POR UN AO O 12 ONZAS DE TABACO DURANTE LA VIDA)?" text={datos.tabaquismoASintR} />
                                        <QuestionnaireTos key={2} title="B. FUMA AHORA (INCLUYE UN MES ATRS)?" text={datos.tabaquismoBSintR} />
                                        <QuestionnaireTos key={3} title="C. A QU EDAD COMENZ A FUMAR EN FORMA REGULAR?" text={datos.tabaquismoCSintR} />
                                        <QuestionnaireTos key={4} title="D. SI YA DEJO DE FUMAR TOTALMENTE, A QU EDAD LO DEJ?" text={datos.tabaquismoDSintR} />
                                        <QuestionnaireTos key={5} title="E. CUANTOS CIGARRILLOS FUMA AL DA O FUMABA?" text={datos.tabaquismoESintR} />
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>

                        <Grid item xs={12}>
                            <Grid container spacing={1}>
                                <Grid item xs={12}>
                                    <Divider />
                                </Grid>

                                <Grid item xs={12}>
                                    <Typography variant='h6'><b>12. ACTIVIDAD DEPORTIVA</b></Typography>
                                </Grid>

                                <Grid item xs={12}>
                                    <Divider />
                                </Grid>

                                <Grid item xs={12}>
                                    <Grid container spacing={0.2}>
                                        <QuestionnaireTos key={1} title="A. REALIZA ALGN DEPORTE?" text={datos.actDeportASintR} />
                                        <QuestionnaireTos key={2} title="- CUAL DEPORTE O ACTIVIDAD?" text={datos.actDeporA1SintR} />
                                        <QuestionnaireTos key={3} title="- DAS A LA SEMANA QUE LO PRACTICA" text={datos.actDeporA2SintR} />
                                        <QuestionnaireTos key={4} title="- HORAS AL DA QUE LE DEDICA" text={datos.actDeporA3SintR} />
                                        <QuestionnaireTos key={4} title="- PROMEDIO DE HORAS A LA SEMANA" text={datos.actDeporA4SintR} />
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>

                        <Grid item xs={12}>
                            <Grid container spacing={1}>
                                <Grid item xs={12}>
                                    <Divider />
                                </Grid>

                                <Grid item xs={12}>
                                    <Typography variant='h6'><b>13. RECOMENDACIONES</b></Typography>
                                </Grid>

                                <Grid item xs={12}>
                                    <Divider />
                                </Grid>

                                <Grid item xs={12}>
                                    <Typography variant='h6'>{datos.recoSintR}</Typography>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>

            <Grid sx={{ pt: 2 }} textAlign="center" justifyContent="center" container spacing={1}>
                <Grid item xs={12}>
                    <Divider sx={{ border: 2, borderRadius: 1, background: ColorDrummondltd.RedDrummond, color: ColorDrummondltd.RedDrummond }} />
                </Grid>

                <Grid item xs={4}>
                    <Typography variant="h6">Fecha Sistema: {FormatDate(new Date())}</Typography>
                </Grid>

                <Grid item xs={4}>
                    <Typography variant="h6">Ibarra Lopez,Melquis Leonardo</Typography>
                </Grid>

                <Grid item xs={4}>
                    <Typography variant="h6">Usuario Activo: {user.email}</Typography>
                </Grid>
            </Grid>
        </SubCard>
    );
};

export default ReportPage3;